.edit-profile-container {
    max-width: 100vw;
    min-height: 100vh;
    height: min-content;
    background-color: var(--main-bg);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
}

.edit-profile-heading {
    width: 100%;
    height: 60px;
    text-align: center;
    margin-top: 125px;
    font-size: 40px;
    font-weight: 600;
}

.edit-profile-info {
    border: 1px solid #ffffff60;
    width: 600px;
    min-height: 500px;
    height: min-content;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    padding: 50px 70px;
    margin-top: 20px;
    margin-bottom: 45px;
}

.edit-container {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-between;
    margin-bottom: 40px;
}

.edit-property {
    font-size: 22px;
    font-weight: 300;
    padding-bottom: 8px;
}

.edit-property-desc {
    font-size: 15px;
    font-weight: 300;
    color: darkgray;
}

.edit-profile-input {
    width: 100%;
    height: 45px;
    border: 1px solid #ffffff70;
    background-color: var(--main-bg);
    padding-left: 10px;
    color: #fff;
}

.input-file {
    width: 100%;
    font-size: 16px;
    cursor: pointer;
}

.input-bio {
    width: 100%;
    height: 100px;
    color: #fff;
    background-color: transparent;
    resize: none;
}


.edit-profile-btn {
    width: 100%;
    height: 50px;
    text-align: center;
    font-size: 17px;
    font-weight: 500;
    background-color: var(--auth-btn-bg);
}